.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.hide {
  display: none;
}

.silent {
  pointer-events: none;
}

.drop-mask {
  position: fixed;
  pointer-events: all;
  flex-direction: column;
  top: 0;
  z-index: 3;
  height: 100%;
  width: 100%;
  display: flex;
  left: 0;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

a:visited {
  color: currentColor;
}

.link {
  color: #09d3ac;
}

.clickable {
  cursor: pointer;
}

.btn {
  cursor: pointer;
  background-color: #09d3ac;
  color: white;
  padding: 6px 14px;
  border-radius: 4px;
  box-shadow: 0 3px 13px rgba(0, 0, 0, 0.12);
  transition: color 300ms;
}

.btn:hover {
  background-color: #08dcb1db;
}

.btn:active {
  background-color: #07b693;

}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  left: 0;
  color: white;
}

small {
  font-family: sans-serif;
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 1.5px;
}

.hover-effect .item {
  display: inline-block;
  vertical-align: bottom;
  width: 0;
  overflow: hidden;
  transition: width .3s;
}

.hover-effect:hover .item,
.hover-effect .item.link {
  width: initial;
}